import React, { useContext } from 'react';
import './main.css';

import AreasConservacion from '../content/areas-conservacion/areas-conservacion';
import Atmosfera from '../content/atmosfera/atmosfera';
import Biodiversidad from '../content/biodiversidad/biodiversidad';
import Residuos from '../content/residuos/residuos';
import Educacion from '../content/educacion/educacion';
import Suelo from '../content/suelo/suelo';
import Agua from '../content/agua/agua';
import Cambio from '../content/cambio/cambio';
import Impacto from '../content/impacto/impacto';
import Bancos from '../content/bancos/bancos';
import Proteccion from '../content/proteccion/proteccion';
import Planeacion from '../content/planeacion/planeacion';
import Demografia from '../content/demografia/demografia';
import MarcoJuridico from '../content/marco-juridico/marcoJuridico';
import Infografias from '../content/infografias/infografias';
import Mapas from '../content/mapas/mapas';
import Zoologico from '../content/zoologico/zoologico';
import Instrumentos from '../content/instrumentos/instrumentos';
import Fotografia from '../content/fotografia/fotografia';
import SistemaIntegral from '../content/sistema-integral/sistema-integral'

import MenuContextual from '../UI/menu-contextual/menu-contextual';
import Enlaces from './enlaces/enlaces';
import Contacto from '../contacto/contacto';
import BanerTexto from '../UI/banerTexto/banerTexto';
import SubMenuMini from './sub-menu-mini/subMenuMini';
import FloatingButton from '../UI/FloatingButton/FloatingButton';

import mapa from './img-mapa-inegi.png';
import { MenuContext } from '../../MenuContext';

function Main(props) {
  const { currentContent } = useContext(MenuContext);
  const objetivos = [
    {
      id: 1,
      titulo: "Objetivo General:",
      parrafo: "Contar un Sistema  de Información Ambiental  y Recursos Naturales en Michoacán que registre, organize, actualize y difunda información en cumplimiento a lo que establece la Ley para la Conservación y Sustentabilidad Ambiental del Estado de Michoacán de Ocampo y demás disposiciones legales en la materia; con el propósito de apoyar en el diseño de políticas públicas, estrategias y toma de decisiones para un desarrollo sustentable.",
    },
    {
      id: 2,
      titulo: "Objetivos Especificos:",
      parrafo: (
        <ul>
          <li>Brindar información en materia ambiental mediante una plataforma virtual,  para contribuir al diseño, evaluación e implementación de políticas, planes, programas e instrumentos de gestión ambiental formulados por las distintas áreas operativas organismos públicos.</li>
          <li>Proporcionar un mapa interactivo como instrumento que ayude a la toma de conciencia y a fomentar la participación ciudadana en torno a los recursos naturales.</li>
          <li>Facilitar el uso e intercambio de la información entre los sectores, gobierno estatal e instituciones públicas que generen y utilicen información ambiental.</li>
          <li>Apoyar la educación ambiental permitiendo el acceso a información ambiental diversa de manera irrestricta y amigable.</li>
          <li>Difudir información de interés ambiental que se sujete a los principios de accesibilidad, transparencia, objetividad e independencia.</li>
        </ul>
      ),
    }
  ];

  switch (currentContent) {
    case 'areas-conservacion':
      return <AreasConservacion />;
    case 'atmosfera':
      return <Atmosfera />;
    case 'biodiversidad':
      return <Biodiversidad />;
    case 'residuos':
      return <Residuos />;
    case 'educacion':
      return <Educacion />;
    case 'suelo':
      return <Suelo />;
    case 'impacto':
      return <Impacto />;
    case 'bancos':
      return <Bancos />;
    case 'agua':
      return <Agua />;
    case 'cambio':
      return <Cambio />;
    case 'proteccion':
      return <Proteccion />;
    case 'planeacion':
      return <Planeacion />;
    case 'contacto':
      return <Contacto />;
    case 'marco-juridico':
      return <MarcoJuridico />;
    case 'infografias':
      return <Infografias />;
    case 'demografia':
      return <Demografia />;
    case 'mapas':
      return <Mapas />;
    case 'zoologico':
      return <Zoologico />;
    case 'instrumentos':
      return <Instrumentos />;
    case 'fotografia':
      return <Fotografia />;
    case 'sistema-integral':
      return <SistemaIntegral />;
    default:
      return (
        <div className='principal'>
          <FloatingButton />
          <h1 className='titulo-main'>SISTEMA INTEGRAL DE INFORMACIÓN AMBIENTAL DE MICHOACÁN</h1>
          <h2 className='subtitulo-main'>GOBIERNO DE MICHOACÁN</h2>
          <div className='main-mapa-container'>
            <div className='main-mapa-info'>
              <div className='main-mapa-titulo'>ATLAS DIGITAL DE INFORMACIÓN AMBIENTAL</div>
              <div className='main-mapa-subtitulo'>Aquí podrás consultar una serie de mapas temáticos con información geográfica sobre el medio ambiente del Estado de Michoacán.</div>
            </div>
            <a className='main-mapa-espace' href="http://localhost" target="_blank" rel="noopener noreferrer">
              <div class="contenedor-img">
                <img className='mapa-prueba' src={mapa} alt="Imagen 1" title="Click para ver mapa" />
                <div class="texto-img">Click para abrir el mapa</div>
              </div>
            </a>
          </div>
          <MenuContextual setContent={props.setContent} />
          <BanerTexto textos={objetivos} />
          <SubMenuMini setContent={props.setContent} />
          <Enlaces />
        </div>
      );
  }
}

export default Main;
